.Container {
  position: absolute;
  bottom: 0;
  right: 20px;

  width: 350px;
  height: 50px;

  /* margin: 20px; */
  border-radius: 4px 4px 0px 0px;

  background-color: #323232;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);

  transition: border-radius 0.4s ease;
}

.Header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;

  z-index: 10;
}

.Header .Text {
  color: rgba(255, 255, 255, 0.87);
  margin: 0 20px;
}

.Header .Icons {
  color: rgba(255, 255, 255, 0.87);
  margin: 0 10px;
}

.List {
  position: absolute;
  bottom: 50px;

  width: 100%;
  min-height: 120px;
  height: fit-content;
  max-height: 150px;

  border-radius: 4px 4px 0px 0px;
  border: 1px #ccc;

  background-color: #ffffff !important;
  /* box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
		0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12); */
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);

  transition: all 0.2s ease-out;

  overflow-y: scroll;
}

.List .ListItem:not(:last-child) {
  border-bottom: 1px solid #ccc;
}

.List .ListItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  justify-items: center;

  height: 35px;

  padding: 0px 15px;
}

.List .ListItem div {
  margin: 5px 5px;
}

.DocumentIcon {
  color: #013e55;
}

.List .TituloArquivo {
  max-width: 90%;
}

.List .DownloadProgressIconSuccess {
  color: rgb(0, 187, 16);
}

.List .DownloadProgressIconSuccess {
  color: rgb(0, 187, 16);
}

.List .DownloadProgressIconError {
  color: #f44336;
}
